.App {
  text-align: center;
  display: grid;
  place-items: center;;
}

.App > main {
  display: grid;
  grid-template-areas: 
  '. .'
  '. .';
  gap:5vw 2vh;
  place-items: center;
  width:30vw;
  margin-bottom: 3vh;
}


.card {
  width:15vw;
  height: 30vh;
  border:5px solid gold;
  border-radius: 5px;
  display: grid;
  place-items: center;
  font-size: 3rem;
  transition: all 0.2s;
  box-shadow: 0 0px 0px 0px #555;
  -webkit-user-select: none;
  cursor: pointer;
}

button {
  border:2px solid black;
  border-radius: 5px;
  font-size: 1.2rem;
  box-shadow: 0 2px 2px #555;
  cursor: pointer;
}

.card:hover, button:hover {
  box-shadow: 0 2px 3px 0px #555;
  transform: scale(1.01);
}


.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
